import React, { Component } from 'react'
import moment from 'moment'
import PropTypes from 'prop-types'
import { TimeRangePicker } from 'antd-extensions'

import Highlight from '../../../Components/Highlight'
import withAPIDoc from '../../../Components/APIDoc'
import PropsDoc from '../../../Components/PropsDoc'

class TimeRangePickerDoc extends Component {
  static propTypes = {
    screenWidth: PropTypes.number
  }

  constructor(props) {
    super(props)

    this.state = {
      multipleLines: false,
      value: {
        type: 'CUSTOMIZE',
        ranges: [1533081600000, 1534377600000]
      }
    }
  }

  _handleChange = value => {
    console.log(`You're selection is ${JSON.stringify(value)}`)
    this.setState({
      value
    })
  }

  render() {
    const { screenWidth } = this.props
    const multipleLines = screenWidth < 640

    return (
      <React.Fragment>
        <h3></h3>

        <Highlight language="jsx">
          {`// jsx
<TimeRangePicker 
  className={ClassName}
  style={Style}
  labels={Labels}
  value={Value}
  onChange={OnChange}
  disabledDate={DisabledDate}
  multipleLines={MultipleLines}
/>`}
        </Highlight>

        <br />

        <PropsDoc
          data={[
            {
              prop: 'labels',
              type: 'Object',
              description: `label { BTN_ALL: '', BTN_LAST_WEEK: '', BTN_LAST_MONTH: '', BTN_CUSTOMIZE: '', PLACEHOLDER_START: '', PLACEHOLDER_END: '' }`
            },
            {
              prop: 'value',
              type: 'Object',
              description: `{ type: PropTypes.oneOf(['ALL', 'LAST_WEEK', 'LAST_MONTH', 'CUSTOMIZE']), ranges: [Date, Date] }`
            },
            {
              prop: 'onChange',
              type: 'Function',
              description: ''
            },
            {
              prop: 'disabledDate',
              type: 'Function',
              description: 'DatePicker'
            },
            {
              prop: 'multipleLines',
              type: 'Boolean',
              description: ''
            }
          ]}
        />

        <br />

        <div className="mapExample">
          <h3></h3>
          <TimeRangePicker
            style={{ display: 'inline-block' }}
            value={this.state.value}
            onChange={this._handleChange}
            disabledDate={current =>
              current &&
              current >
                moment()
                  .endOf('day')
                  .subtract(1, 'days')
            }
            multipleLines={multipleLines}
          />
          <br />
          <br />
          <p> [ {this.state.value.ranges.map(r => moment(r).format('YYYY-MM-DD HH:mm:ss')).join(', ')} ]</p>
          <br />

          <Highlight language="jsx">
            {`import React from 'react'
import { TimeRangePicker } from 'antd-extensions'

export default class Example extends React.Component {
  
  constructor(props){
    super(props)

    this.state = {
      value: {
        type: 'CUSTOMIZE',
        ranges: [1533081600000, 1534377600000]
      }
    }
  }

  _handleChange = value => {
    console.log(\`You're selection is \${JSON.stringify(value)}\`)
    this.setState({
      value
    })
  }

  render() {
    return (
      <TimeRangePicker
        value={this.state.value}
        onChange={this._handleChange}
        disabledDate={current =>
          current &&
          current >
            moment()
              .endOf('day')
              .subtract(1, 'days')
        }
      />
    )
  }
}
`}
          </Highlight>
        </div>
      </React.Fragment>
    )
  }
}

export default withAPIDoc(TimeRangePickerDoc)
