import React, { Component } from 'react'
import { Button } from 'antd'
import { InputDialog } from 'antd-extensions'

import Highlight from '../../../Components/Highlight'
import withAPIDoc from '../../../Components/APIDoc'
import PropsDoc from '../../../Components/PropsDoc'

class InputDialogDoc extends Component {
  constructor(props) {
    super(props)

    this.state = {
      visible: false,
      value: ''
    }
  }

  toggleDialog = bool => {
    this.setState({
      visible: bool
    })
  }

  confirm = () => {
    return new Promise(resolve => {
      setTimeout(() => {
        resolve()
        this.toggleDialog(false)
      }, 2000)
    })
  }

  render() {
    return (
      <React.Fragment>
        <h3></h3>

        <Highlight language="jsx">
          {`// jsx
<InputDialog 
  labels={Labels}
  value={Value}
  onConfirm={OnChange}
  onCancel={DisabledDate}
  multipleLines={MultipleLines}
  validator={Validator}
  visible={Visible}
/>`}
        </Highlight>

        <br />

        <PropsDoc
          data={[
            {
              prop: 'labels',
              type: 'Object',
              description: (
                <React.Fragment>
                  label
                  <code className="codeRef">
                    &#123; title: '', subTitle: undefined, placeholder: undefined, confirm: '', cancel:
                    '' &#125;
                  </code>
                </React.Fragment>
              )
            },
            {
              prop: 'value',
              type: 'String',
              description: ``
            },
            {
              prop: 'maskClosable',
              type: 'Boolean',
              description: ''
            },
            {
              prop: 'visible',
              type: 'Boolean',
              description: ''
            },
            {
              prop: 'validator',
              type: 'Function',
              description: (
                <React.Fragment>
                  callback 
                  <code className="codeRef">function(value, callback)</code>
                </React.Fragment>
              )
            },
            {
              prop: 'onConfirm',
              type: 'Function => void || Promise',
              description: (
                <React.Fragment>
                  
                  <code className="codeRef">validator</code>
                  
                </React.Fragment>
              )
            },
            {
              prop: 'onCancel',
              type: 'Function',
              description: (
                <React.Fragment>
                  
                  <code className="codeRef">maskClosable = true</code>
                  
                </React.Fragment>
              )
            }
          ]}
        />

        <br />

        <div className="mapExample">
          <h3></h3>
          <InputDialog
            visible={this.state.visible}
            value={this.state.value}
            labels={{ title: '', subTitle: ' ' }}
            onCancel={() => this.toggleDialog(false)}
            onConfirm={this.confirm}
            validator={(val, cb) => {
              if (val === '') {
                return cb()
              }
              return cb('')
            }}
          />
          <Button onClick={() => this.toggleDialog(true)}></Button>

          <br />
          <br />

          <Highlight language="jsx">
            {`import React from 'react'
import { Button } from 'antd'
import { InputDialog } from 'antd-extensions'

export default class Example extends React.Component {
  
  constructor(props){
    super(props)

    this.state = {
      visible: false,
      value: ''
    }
  }

  toggleDialog = bool => {
    this.setState({
      visible: bool
    })
  }

  confirm = () => {
    return new Promise(resolve => {
      setTimeout(() => {
        resolve()
        this.toggleDialog(false)
      }, 2000)
    })
  }

  render() {
    return (
      <div>
        <InputDialog
          visible={this.state.visible}
          value={this.state.value}
          labels={{ title: '', subTitle: ' ' }}
          onCancel={() => this.toggleDialog(false)}
          onConfirm={this.confirm}
          validator={(val, cb) => {
            if (val === '') {
              return cb()
            }
            return cb('')
          }}
        />
        <Button onClick={() => this.toggleDialog(true)}></Button>
      </div>
    )
  }
}
`}
          </Highlight>
        </div>
      </React.Fragment>
    )
  }
}

export default withAPIDoc(InputDialogDoc)
